.absolute {
	position: absolute;
}

.relative {
	position: relative;
}

.fixed {
	position: fixed;
}

.sticky {
	position: sticky;
}

/*
	Position

	  <div class="p-b_3"><span class="bg_accent br_radius c_white d_inline-block font_n1 p-7_3 p-x_5 sg">Is Responsive: breakpoint modifiers</span></div>Quickly alter the position type and location of an object with 'absolute' and 'top'

	.t_0 - modify top of an absolute positioned block 
	.t_5 - modify top of an absolute positioned block
	.b_0 - modify bottom of an absolute positioned block 
	.b_5 - modify bottom of an absolute positioned block
	.l_0 - modify left of an absolute positioned block 
	.l_5 - modify left of an absolute positioned block
	.r_0 - modify right of an absolute positioned block 
